



















































@import url('https://fonts.googleapis.com/css?family=Muli:400,600');

.vimeography-thumbnail {
  margin: 0;
  width: 100%;
}

/* IE11 Support */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .vimeography-thumbnail {
    flex: 0 0 25%;
  }
}

.vimeography-link {
  display: block;
  margin: 0 0 20px;
  border-radius: 3px;
}

.vimeography-link-active {
}

.vimeography-thumbnail-img {
  opacity: 0.9;
  transition: all 250ms ease-in-out;
  border-radius: 3px;
  width: 100%;
  padding: 0;
  box-shadow: #000 0em 0em 0em; /* Fixes weird firefox rendering with opacity changes */
  cursor: pointer;

  &:hover {
    opacity: 1;
  }
}

figcaption {
  text-align: left;
  position: relative;
  overflow: hidden;
}

.vimeography-title,
/deep/ .vimeography-download {
  color: #222;
  font-family: "Muli", sans-serif;
  font-size: 0.8rem;
  line-height: 1.4rem;
  overflow: hidden;
  font-weight: 600;
  margin: 0;
  padding: 0;
  display: block;
}
