



























































@import url('https://fonts.googleapis.com/css?family=Muli:400,600');

.vimeography-gallery {
  width: 90%;
  margin: 0 auto 1rem;
}

/deep/ .vimeography-player-container {
  margin-bottom: 2rem !important;
}

.vimeography-info {
  text-align: center;
}

.vimeography-title {
  color: #222;
  font-family: "Muli", sans-serif;
  font-size: 1.4rem;
  line-height: 1.4rem;
  overflow: hidden;
  font-weight: 600;
  margin: 0;
  padding: 0;
  display: block;

  &:after {
    content: '';
    width: 30px;
    height: 1px;
    background-color: #999;
    display: block;
    margin: 1rem auto;
  }
}

.vimeography-description {
  font-family: "Muli", sans-serif;
  font-size: 1rem;
  line-height: 1.4rem;
  padding-bottom: 1rem;
  max-width: 25rem;
  margin: 0 auto 2rem;
}
